import React from "react";
import classes from "./AboutUs.module.css";
import { images } from "../../constants/images";
import { useNavigate } from "react-router-dom";

const AboutUs = () => {
  const navigate = useNavigate();

  const toAboutUsHandler = () => {
    navigate("/about-us", { replace: true });
  };
  return (
    <section
      className={`section-padding app-bg  flex-center ${classes.aboutus}`}
      id="about"
    >
      <figure className={`flex-center ${classes[`aboutus-overlay`]}`}>
        <img src={images.G} alt="Gilani's letter Logo" />
      </figure>
      <div className={`flex-center ${classes[`aboutus-content`]}`}>
        <div className={classes[`aboutus-content-about`]}>
          <h1 className="headtext-cormorant">About Us</h1>
          <img src={images.spoon} alt="spoon" className="spoon-img" />
          <p className="p-opensans">
            For over 65 years the foremost and the fashionable have been
            dropping in. A filling lunch of traditional italian dishes, Pasta
            meats and vegetables and a flute of fizz with friends, before the
            theatre. Gilani's is relaxed, witty but serious about delivering the
            real Italian experience.
          </p>
          <button
            onClick={toAboutUsHandler}
            type="button"
            className="custom-button"
          >
            Know More
          </button>
        </div>

        <div
          className={`flex-center ${classes[`aboutus-content-about-knife`]}`}
        >
          <img src={images.knife} alt="about_knife" />
        </div>

        <div className={classes[`aboutus-content-history`]}>
          <h1 className="headtext-cormorant">Our History</h1>
          <img src={images.spoon} alt="spoon" className="spoon-img" />
          <p className="p-opensans">
            Gilani's Restaurant was founded in 1957 in London, UK. Our journey
            into the restaurant business in London started over 65 years ago,
            with the opening our first location, as an authentic Italian
            restaurant in Portobello.
          </p>
          <button
            onClick={toAboutUsHandler}
            type="button"
            className="custom-button"
          >
            Know More
          </button>
        </div>
      </div>
    </section>
  );
};

export default AboutUs;
