import React, { useRef } from "react";
import {
  BsInstagram,
  BsArrowLeftShort,
  BsArrowRightShort,
} from "react-icons/bs";
import classes from "./Gallery.module.css";
import SubHeading from "../Header/SubHeading";
import { images } from "../../constants/images";

const Gallery = () => {
  const scrollRef = useRef(null);
  const scroll = (direction) => {
    const { current } = scrollRef;

    if (direction === `left`) {
      current.scrollLeft -= 300;
    } else {
      current.scrollRight += 300;
    }
  };

  const galleryImages = [
    images.gallery01,
    images.gallery02,
    images.gallery03,
    images.gallery04,
  ];

  const socialMediaHandler = () => {
    window.location = `https://instagram.com`;
  };

  return (
    <section className={`${classes.gallery} flex-center section-padding`}>
      <div className={classes[`gallery-content`]}>
        <SubHeading title="Social Media" />
        <h1
          style={{ marginTop: `2rem`, marginLeft: 0 }}
          className="headtext-cormorant"
        >
          Photo Gallery
        </h1>
        <p
          className="p-opensans"
          style={{ color: `#AAA`, marginTop: `2rem`, marginLeft: 0 }}
        >
          {" "}
          Check out the latests images from customers who have visited Gilani's
          Restaurant.
        </p>
        <button
          onClick={socialMediaHandler}
          className="custom-button"
          type="button"
        >
          View More
        </button>
      </div>

      <div className={classes[`gallery-images`]}>
        <div className={classes[`gallery-images-container`]} ref={scrollRef}>
          {galleryImages.map((image, i) => (
            <div
              className={`flex-center ${classes[`gallery-images-card`]}`}
              key={`gallery-image-${i + 1}`}
            >
              <img src={image} alt="gallery" />
              <BsInstagram className={classes[`gallery-image-icon`]} />
            </div>
          ))}
        </div>
        <div className={classes[`gallery-images-arrows`]}>
          <BsArrowLeftShort
            className={classes[`gallery-arrows-icon`]}
            onClick={() => scroll(`left`)}
          />
          <BsArrowRightShort
            className={classes[`gallery-arrows-icon`]}
            onClick={() => scroll(`right`)}
          />
        </div>
      </div>
    </section>
  );
};

export default Gallery;
