import React from "react";
import classes from "./Laurels.module.css";
import SubHeading from "../Header/SubHeading";
import { awards } from "../../constants/data";
import { images } from "../../constants/images";

const AwardCard = ({ award: { imgUrl, title, subtitle } }) => {
  return (
    <div className={classes[`laurels-awards-card`]}>
      <img src={imgUrl} alt="award" />
      <div className={classes[`laurels-awards-card-content`]}>
        <p style={{ color: `#DCCA87` }} className="p-cormorant">
          {title}
        </p>
        <p className="p-cormorant">{subtitle}</p>
      </div>
    </div>
  );
};

const Laurels = () => {
  return (
    <section className="app-bg app-wrapper section-padding" id="awards">
      <div className="app-wrapper_info">
        <SubHeading title="Awards & Recognition" />
        <h1 className="headtext-cormorant"> Our Laurels</h1>
        <div className={classes[`laurel-awards`]}>
          {awards.map((award) => (
            <AwardCard award={award} key={award.title} />
          ))}
        </div>
      </div>
      <figure className="app-wrapper_img">
        <img src={images.laurels} alt="resturant awards" />
      </figure>
    </section>
  );
};

export default Laurels;
